'use client';
import { useEffect, useState } from 'react';
import { useToast } from "@/hooks/use-toast";
import { jsPDF } from "jspdf";
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Skeleton } from '@/components/ui/skeleton';
import { EventCategory } from '@/types/type';
import { useDebounce } from 'use-debounce';
import { motion, AnimatePresence } from 'framer-motion';
import { ChevronLeft, ChevronRight, Clock3, DollarSign, Download, Flame, School, School2Icon, Search } from 'lucide-react'; import EventCard from '@/components/ui/EventCard';

const filters = [{ label: "All", icon: School2Icon }, { label: "Upcoming", icon: Flame }, { label: "Past", icon: Clock3 },];

const UserDashboard = () => {
    const { toast } = useToast(); const [events, setEvents] = useState<EventCategory[]>([]); const [activeFilter, setActiveFilter] = useState<string>('all'); const [searchQuery, setSearchQuery] = useState(''); const [debouncedQuery] = useDebounce(searchQuery, 300); const [currentPage, setCurrentPage] = useState(1); const [eventsPerPage] = useState(6); const [loading, setLoading] = useState(false);

    const generateInvoice = (event: EventCategory) => {
        const doc = new jsPDF();

        doc.setFontSize(16);
        doc.text("Booking Invoice", 20, 20);

        doc.setFontSize(12);
        doc.text(`Student Name: Test User`, 20, 40);
        doc.text(`Student Email: test@example.com`, 20, 50);

        doc.text(`Event Name: ${event.title}`, 20, 70);
        doc.text(`Location: ${event.location}`, 20, 80);
        doc.text(`Date: ${new Date(event.date).toLocaleDateString()}`, 20, 90);

        doc.text(`Booking ID: #${event.registeringId || "123456"}`, 20, 110);
        doc.text("Thank you for booking with us!", 20, 130);

        doc.save(`Invoice-${event.title}.pdf`);
    };

    const fetchEvents = async () => {
        setLoading(true);
        const type = activeFilter === 'all' ? '' : activeFilter;
        try {
            const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/users/me/registered-events?type=${type}&page=${currentPage}&perPage=${eventsPerPage}`, {
                method: 'GET',
                credentials: 'include'
            });
            const data = await res.json();
            setEvents(data.events);
        } catch (error: any) {
            toast({
                title: "Error",
                description: error?.message || "Failed to fetch events.",
                variant: "destructive",
            });
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchEvents();
    }, [activeFilter, currentPage]);

    const filteredEvents = events.filter(event =>
        event.title.toLowerCase().includes(debouncedQuery.toLowerCase())
    );

    const indexOfLastEvent = currentPage * eventsPerPage;
    const indexOfFirstEvent = indexOfLastEvent - eventsPerPage;
    const currentEvents = filteredEvents.slice(indexOfFirstEvent, indexOfLastEvent);
    const totalPages = Math.ceil(filteredEvents.length / eventsPerPage);

    return (
        <section className="px-4 py-6">
            <h1 className="text-2xl font-bold mb-6 text-center text-purple-800">🎉 My Booked Events</h1>

            <div className="flex flex-col md:flex-row gap-4 justify-between items-center mb-6">
                <div className="relative w-full md:w-1/3">
                    <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                        <Search className="h-5 w-5 text-purple-400" />
                    </div>
                    <Input
                        placeholder="Search events..."
                        className="pl-10 pr-4 py-2 rounded-full border border-purple-300 focus:ring-2 focus:ring-purple-500 focus:border-transparent w-full"
                        value={searchQuery}
                        onChange={(e) => {
                            setSearchQuery(e.target.value);
                            setCurrentPage(1);
                        }}
                    />
                </div>

                <div className="flex flex-wrap gap-2 justify-center">
                    {filters.map(({ label, icon: Icon }) => {
                        const key = label.toLowerCase();
                        const isActive = activeFilter === key;
                        return (
                            <motion.button
                                key={key}
                                whileHover={{ scale: 1.05 }}
                                onClick={() => setActiveFilter(key)}
                                className={`flex items-center gap-2 px-4 py-2 rounded-full transition-colors
                                ${isActive ? 'bg-purple-600 text-white' : 'bg-gray-200 text-purple-700 hover:bg-purple-100'}`}
                            >
                                <Icon size={18} /> {label}
                            </motion.button>
                        );
                    })}
                </div>
            </div>

            {loading ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {[...Array(3)].map((_, i) => <Skeleton key={i} className="h-[300px] w-full rounded-xl" />)}
                </div>
            ) : filteredEvents.length === 0 ? (
                <p className="text-center text-gray-500 mt-10">You haven't booked any events yet.</p>
            ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {currentEvents.map(event => (
                        <EventCard key={event.id} event={event} className="">
                            <div className="p-4 space-y-2">
                                <h3 className="text-xl font-semibold text-purple-700">{event.title}</h3>
                                <p className="text-sm text-gray-600">{event.description}</p>
                                <p className="text-sm text-gray-500">📅 {new Date(event.date).toLocaleDateString()}</p>
                                <p className="text-sm text-gray-500">📍 {event.location}</p>
                                <Button onClick={() => generateInvoice(event)} className="mt-3 bg-purple-500 text-white">
                                    <Download size={16} /> Download Invoice
                                </Button>
                            </div>
                        </EventCard>
                    ))}
                </div>
            )}

            {filteredEvents.length > eventsPerPage && (
                <div className="flex justify-center mt-8">
                    <nav className="flex items-center gap-2">
                        <button onClick={() => setCurrentPage(p => Math.max(1, p - 1))} disabled={currentPage === 1} className="p-2 rounded-full hover:bg-purple-100">
                            <ChevronLeft className="text-purple-400" />
                        </button>
                        {Array.from({ length: totalPages }).map((_, index) => (
                            <button
                                key={index}
                                onClick={() => setCurrentPage(index + 1)}
                                className={`w-10 h-10 rounded-full flex items-center justify-center
                                ${currentPage === index + 1 ? 'bg-purple-600 text-white' : 'text-purple-500 hover:bg-purple-100'}`}
                            >
                                {index + 1}
                            </button>
                        ))}
                        <button onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))} disabled={currentPage === totalPages} className="p-2 rounded-full hover:bg-purple-100">
                            <ChevronRight className="text-purple-400" />
                        </button>
                    </nav>
                </div>
            )}
        </section>
    );

};

export default UserDashboard;